import React, { useRef, useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { IconButton } from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import { PatternFormat } from "react-number-format";
import Swal from "sweetalert2";
import { firebaseMiddleware } from "../../store/middleware/firebase";
import "./ClienteList.css";
import Fab from "@mui/material/Fab";
import EditIcon from "@mui/icons-material/Edit";
import AddCliente from "../AddCliente/AddCliente";

function ListaDeClientes({ cliente }) {
  const dispatch = useDispatch();
  const clienteRef = useRef(null);
  const [showEdit, setShowEdit] = useState(false);

  // Quando o componente atualiza e est destacado, faz o scroll
  useEffect(() => {
    if (cliente.destacado && clienteRef.current) {
      clienteRef.current.scrollIntoView({
        behavior: "smooth", // animao suave
        block: "center", // centraliza o elemento
        inline: "nearest", // evita scroll horizontal desnecessrio
      });
      clienteRef.current.classList.add("highlight");
      setTimeout(() => {
        clienteRef.current?.classList.remove("highlight");
      }, 1000);
    }
  }, [cliente.destacado]);

  const verificarAvisoDeRetirada = () => {
    const dataAtual = new Date();
    const dataInicio = new Date(cliente.data);
    const diferencaTempo = dataAtual - dataInicio;
    const diferencaDias = Math.floor(diferencaTempo / (1000 * 60 * 60 * 24));
    return diferencaDias >= 4;
  };

  const obterCorDeFundo = () => {
    if (cliente.destacado) {
      return "#808080"; // Cinza para cliente buscado
    } else if (cliente.concluido) {
      return "#008000"; // Verde para concludo
    } else if (verificarAvisoDeRetirada()) {
      return "#FF6347"; // Vermelho se passaram mais de 4 dias
    } else {
      return "#ffe4c4"; // Amarelo padro
    }
  };

  const estilo = {
    backgroundColor: obterCorDeFundo(),
    color:
      cliente.destacado || cliente.concluido || verificarAvisoDeRetirada()
        ? "white"
        : "black",
  };

  const handleRemoveClient = async () => {
    try {
      const result = await Swal.fire({
        title: "Tem certeza?",
        text: "Voc no poder reverter isso!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#0b8a15",
        cancelButtonColor: "#d33",
        confirmButtonText: "Sim, delete!",
        cancelButtonText: "Cancelar",
      });

      if (result.isConfirmed) {
        await dispatch(firebaseMiddleware.removeClient(cliente));
        Swal.fire("Deletado!", "O cliente foi removido.", "success");
      }
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Erro ao remover cliente",
        text: error.message,
      });
    }
  };

  const handleStatusChange = async (estaMarcado) => {
    try {
      await dispatch(
        firebaseMiddleware.updateClientStatus(cliente, estaMarcado)
      );
      Swal.fire({
        icon: "success",
        title: estaMarcado
          ? "Cliente marcado como concludo!"
          : "Cliente desmarcado",
        timer: 1500,
        showConfirmButton: false,
      });
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Erro ao atualizar status",
        text: error.message,
      });
    }
  };

  const handleClick = () => {
    setShowEdit(true);
  };

  return (
    <div className="list" ref={clienteRef}>
      <ul style={estilo}>
        <li style={estilo}>
          NIF: {""}
          <PatternFormat
            value={cliente.nif}
            displayType="text"
            format="###-###-###"
          />
        </li>
        <li style={estilo}>NOME: {cliente.nome || "Sem nome"}</li>
        <li style={estilo}>
          TELEMVEL:{" "}
          <PatternFormat
            value={cliente.contato}
            displayType="text"
            format="###-###-###"
          />
        </li>
        <li style={estilo}>LOCAL: {cliente.local || "No especificado"}</li>
        <li style={estilo}>DATA: {cliente.data || "No especificado"}</li>
        <li style={estilo}>HORA: {cliente.hora || "No especificado"}</li>
        <li style={estilo}>TIPO: {cliente.tipo || "No especificado"}</li>
        <li style={estilo}>
          VALOR: {cliente.valor ? `${cliente.valor} ` : "No especificado"}
        </li>

        <div className="btn-list">
          <IconButton
            type="button"
            aria-label="delete"
            onClick={handleRemoveClient}
          >
            <DeleteIcon />
          </IconButton>
        </div>

        <input
          className="checkbox"
          type="checkbox"
          checked={cliente.concluido || false}
          onChange={(e) => handleStatusChange(e.target.checked)}
        />

        {showEdit && (
          <div className="modal-overlay">
            <div className="modal-content">
              <AddCliente
                clienteParaEditar={cliente}
                onClose={() => setShowEdit(false)}
                isEditing={true}
              />
            </div>
          </div>
        )}

        <div className="edit">
          <Fab
            size="medium"
            color="primary"
            aria-label="edit"
            onClick={handleClick}
          >
            <EditIcon />
          </Fab>
        </div>
      </ul>
    </div>
  );
}

export default ListaDeClientes;
